import React, {Component} from "react";
import PropTypes from "prop-types";
export default class InfoComponent extends Component {

    colorClass() {
        switch (this.props.color) {
            case "black":
                return  " g-black1-c ";
            case "grey":
                return " g-gray5-c ";
            case "blue":
                return " g-blue3-c ";
            default:
                return "";
        }
    }
    bgColorClass() {
        switch (this.props.bgColor) {
            case "none":
                return "";
            case "grey":
                return "g-gray1-bg";
            case "white":
                return " g-white1-bg ";
            default:
                return " g-white1-bg ";
        }
    }
    textColor() {
        switch (this.props.textColor) {
            case "black":
                return " g-black1-c ";
            case "grey":
                return " g-gray6-c ";
            default:
                return " g-gray6-c ";
        }
    }

    paddingClass = ({padding}) => ({
        noPadding: "u-no-padding-l",
        default: "u-padding-l",
    })[padding || "default"];

    render() {
        if (this.props.type === "blueInfo") {
            return (
                <blockquote className={"opl-blockquote g-bluef-bdrc u-box-shadow--s " + this.paddingClass(this.props)}>
                    <div className={"opl-msg opl-msg--box opl-msg--info  " + this.colorClass() + this.bgColorClass()}>
                        <div className="o-icon-list">
                            <div className="o-icon-list__item"/>
                            <div className="o-icon-list__icon u-vertical-top u-padding-right">
                                <div className="g-icon g-icon--info g-icon--before g-icon--s g-bluef-c"
                                     aria-hidden="true"/>
                            </div>
                            <div className="o-icon-list__text">
                                <h5 className="u-no-margin"> {this.props.title} </h5>
                                <p>{this.props.children}</p>
                            </div>
                        </div>
                    </div>
                </blockquote>
            );
        }
        return (
            <div
                className={"o-icon-text g-icon g-icon--info g-icon--before g-icon--xs-s u-padding-l u-padding-top-l " + this.colorClass() + this.bgColorClass()}>
                <p className={"o-icon-text__text " + this.textColor()}>{this.props.children}</p>
            </div>
        );
    }
}

InfoComponent.propTypes = {
    color: PropTypes.string,
    bgColor: PropTypes.string,
    title: PropTypes.string,
    type: PropTypes.string,
    padding: PropTypes.string,
    textColor: PropTypes.string,
};

InfoComponent.defaultProps = {
    color: "black",
    bgColor: "white",
    textColor: "grey",
};
export const ConditionalInfoComponent = props => {
    const {condition = false, text = "", altText = "", ...rest} = props;
    return (
        <InfoComponent {...rest}>
            {condition ? text : altText}
        </InfoComponent>
    );
};

ConditionalInfoComponent.propTypes = {
    condition: PropTypes.bool,
    text: PropTypes.string,
    altText: PropTypes.string,
};

